* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	appearance: none;
}
$black: #000000;
$darkgrey: #303030;
$white: #ffffff;


@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

@font-face {
	font-family: 'Icon';
	src: 
		local('fonts/icon'), url('../media/fonts/icon/icon.ttf'),
		local('fonts/icon'), url('../media/fonts/icon/icon.woff2');
	font-display: block;
}
* {
	color: $darkgrey;
}
* {
	font-family: 'Inter', sans-serif;
	font-size: 18px;
	line-height: 1.5
}

.icon {
  display: inline-block;
  font: normal normal normal 1em/1 'Icon';
  color: inherit;
  flex-shrink: 0;
  speak: none;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

$icons: (
  arrow: 'ea02',
  moon: 'ea03',
  sun: 'ea04',
  reload: 'ea05',
);

@function unicode($str) {
  @return unquote("\"\\#{$str}\"");
}

@each $name, $code in $icons {
  .icon--#{"" +$name}::before {
    content: unicode($code);
  }
}
.clock {
  border: 1px solid red;
  
  &__inner {
    display: grid;
    height: 100vh;
    grid-template-areas: 
      "quote ."
      "greeting . "
      "time ."
      "location toggle";
    grid-template-columns: auto 160px;
    grid-template-rows: 1fr auto auto auto;
    justify-content: start;
    align-items: start;
    padding: 56px 160px;
  }

  &__quote, .quote {
    grid-area: quote;
    
    &__text {
      color: $white;
      margin-bottom: 12px;

      &::before { content: "\""; }
      &::after { content: "\""; }
    }

    &__author, .author {
      color: $white;
      font-weight: 700;

    }

  }

  &__greeting, .greeting {
    grid-area: greeting;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

    &__icon {
      color: $white;
      margin-right: 16px;
    }

    &__text {
      color: $white;
    }

  }

  &__time, .time {
    grid-area: time;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-end;
    
    
    &__time {
      color: $white;
      margin-right: 32px;
      font-size: 200px;
      font-weight: 700;
      line-height: 1;
    }

    &__timezone {
      color: $white;
      margin-bottom: 12px;
      font-size: 40px;
    }

  }

  &__location, .location {
    grid-area: location;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;


    &__city {
      color: $white;
      text-transform: uppercase;
      margin-right: 16px;
      font-size: 24px;
      font-weight: 700;

      &::after {
        content: ",";
      }
    }

    &__country {
      color: $white;
      text-transform: uppercase;
      font-size: 24px;
      font-weight: 700;
    }
  }

  &__toggle, .toggle {
    background-color: $white;
    grid-area: toggle;
    width: 142px;
    height: 56px;
    border-radius: 56px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 8px 8px 8px 24px;
    cursor: pointer;

    &__input, .input {
      width: 40px;
      height: 40px;
      background-color: $darkgrey;
      border-radius: 50%;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-content: center;

      &__icon {
        color: $white;
        font-size: 24px;
        align-self: center;
        transform: rotate(180deg);
      }

    }

    &__label {
      line-height: 1;
      font-size: 18px; 
      margin-top: 4px;
      font-weight: 700;
      letter-spacing: 4px;
      color: $black;
      opacity: 0.5;
    }

  }

}
.page {
	background-image: url('/media/images/background-day.webp');
	width: 100vw;
	min-height: 100vh;
	background-size: cover;
	background-position: center;
}
.panel {
  border: 10px solid blue;

}